import React from "react"
import Helmet from "react-helmet"
import parse from "html-react-parser"

function SEO({ data }) {
  let headHTML = ""

  Object.keys(data).forEach(key => {
    headHTML += data[key];
  });

  return (
    <Helmet> 
      { parse(headHTML) }
    </Helmet>
  )
}

export default SEO
