import React, { useEffect, useRef } from 'react';
import { Link } from "gatsby";
import SVGLogo from './svgLogo';
import NormalizeWheel from '../utils/normalizeWheel';

const checkWindow = (typeof window === `undefined`) ? false : true; // gatsby cannot have window called while building the pages

const Nav = () => {
  const navEl = useRef(),
        duration = 400,
        isSafari = checkWindow ? /^((?!chrome|android).)*safari/i.test(navigator.userAgent) : false;
        // isIE = !!navigator.userAgent.match(/Trident/g) || !!navigator.userAgent.match(/MSIE/g);

  useEffect(() => {
    let navHidden = false,
        scrolling = false,
        windowH = checkWindow ? window.innerHeight : 0,
        scrollThreshold = -5;

    // even with the normaliseWheel, Safari seems to give different values to other browsers
    if(isSafari){
      scrollThreshold = 0;
    }

    const NavSlideScroll = e => {
      if(!scrolling){
        window.requestAnimationFrame(() => {
          let top = window.pageYOffset;

          if(!navHidden && top < 10){
            let normaliseWheelY = NormalizeWheel.init(e);

            if(normaliseWheelY > 0){
              e.preventDefault();
              navHidden = true;
              scrolling = true;
              setTimeout(() => {
                scrolling = false;
              },duration)
      
              navEl.current.style.opacity = 0;
              navEl.current.classList.add('-hidden');
      
              // added to lock to top
              window.scroll({ 
                top: 0,
                behavior: 'smooth' 
              })
            }
            // scrollThreshold greater than -10 to prevent accidental nav reveals
          } else if(top < (windowH * 0.1) && navHidden) {
            let normaliseWheelY = NormalizeWheel.init(e);

            if(normaliseWheelY < scrollThreshold){
              e.preventDefault();
              navHidden = false;
              scrolling = true;
              setTimeout(() => {
                scrolling = false;
              },duration)
      
              navEl.current.style.opacity = 1;
              navEl.current.classList.remove('-hidden');
      
              window.scroll({ 
                top: 0,
                behavior: 'smooth' 
              })
            }
          }
        })
      }
    }
        
    if(checkWindow){
      if(navEl.current !== undefined && navEl.current.classList.contains('-hidden')){
        window.scrollTo(0, 0);
      } else {
        window.scrollTo(0, 6);
      }

      window.addEventListener("wheel", NavSlideScroll);

      return () => {
        window.removeEventListener("wheel", NavSlideScroll);
      }
    }
  })

  return(
    <nav className="nav" ref={navEl}>
      <div className="nav__drop-shadow"/>

      <div className="nav__contain">
        <Link to="/">
          <SVGLogo />
        </Link>

        <div className="nav__links">
          <Link className="nav__link" activeClassName='-active' to="/work">
            <p className="nav__link-text fontSize-p2">Work</p>
          </Link>
          <Link className="nav__link" activeClassName='-active' to="/about">
            <p className="nav__link-text fontSize-p2">About</p>
          </Link>
          <Link className="nav__link" activeClassName='-active' to="/contact">
            <p className="nav__link-text fontSize-p2">Contact</p>
          </Link>
        </div>
      </div>
    </nav>
  )
};

export default Nav;